#login{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#login form{
    display: flex;
    flex-direction: column;
}

#login form h1{
    font-family: 'Rubik', sans-serif;
    font-size: 2em;
    text-align: center;
}

#login form h2{
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    text-align: center;
    margin-bottom: 1em;
}

#login form h2 > a{
    font-weight: 400;
    color: inherit;
}

#login form input{
    padding: 0.5em;

    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    font-size: 1.2em;

    text-align: center;
    border: 1px solid #ccc;
    border-radius: 10px 10px 0px 0px;
}

#login form button{
    padding: 0.5em;
    cursor: pointer;
    border: none;
    background-color: #6354af;
    color: white;
    
    font-family: 'Rubik', sans-serif;
    font-weight: 500;
    font-size: 1.2em;
    border-radius: 0px 0px 10px 10px;
}