@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500|Rubik:300,400,500,700&display=swap);
*{
    margin: 0;
    padding: 0;
    outline: none;
    box-sizing: border-box;
}

html, body, #root{
    height: 100%;
}

#app{
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    width: 90%;
    margin: 0 auto;
    font-family: 'Roboto', sans-serif;
    font-size: 1em;
    font-weight: 300; /* light */
}

h1, h2{
    margin: 0;
    padding: 0;
}

a{
    text-decoration: none;
}

strong{
    font-weight: 500;
}

header{
    padding: 1em 0;
    display: flex;
    /* flex-wrap: wrap; */
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}

header img{
    box-sizing: border-box;
    width: 7em;
    height: 7em;
    box-shadow: 0px 4px 4px rgba(48, 43, 43, 0.25);
    border-radius: 50%;
    margin-right: 1em;
}

header h1{
    font-family: 'Rubik', sans-serif;
    font-size: 1.953em;
    font-weight: bold;
    text-align: center;
    margin-top: 0.5em;
}

header h2{
    font-family: 'Roboto', sans-serif;
    font-size: 1.563em;
    font-weight: 100; /* thin */
    text-align: center;
}

section{
    width: 100%;
    margin-bottom: 10px;
}

section h3{
    font-family: 'Rubik', sans-serif;
    font-size: 1.25em;
    font-weight: 500; /* medium */
    border-bottom: 1px solid rgba(0, 0, 0, 0.25);
    margin-bottom: 10px;
}

.projects{
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 2em;
}

@media (min-width: 600px){
    header{
        justify-content: flex-start;
        padding-top: 3em;
        flex-direction: row;
    }
    header h1, header h2{
        text-align: left;
    }
    .projects{
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 900px){
    .projects{
        grid-template-columns: repeat(3, 1fr);
    }
}
.project-card{
    border-radius: 25px;
    box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.25);
    background-color: #ffffff;
}

.project-image{
    width: 100%;
    border-radius: 25px 25px 0 0 ;
}

.project-card h4{
    font-family: 'Roboto', sans-serif;
    font-size: 1.563em;
    font-weight: bold;
    margin: 0.5em 0.5em 1em 0.5em;
}

.project-card a{
    color: #000000;
}

.project-card p{
    margin: 0.5em 0.5em 1em 0.7em;
}

.project-description{
    font-family: 'Roboto', sans-serif;
    font-size: 1em;
    font-weight: 300; /* light */
}

.project-language{
    font-family: 'Roboto', sans-serif;
    font-weight: 100; /* thin */
}

.project-repo{
    display: flex;
    align-items: center;
}
#login{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#login form{
    display: flex;
    flex-direction: column;
}

#login form h1{
    font-family: 'Rubik', sans-serif;
    font-size: 2em;
    text-align: center;
}

#login form h2{
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    text-align: center;
    margin-bottom: 1em;
}

#login form h2 > a{
    font-weight: 400;
    color: inherit;
}

#login form input{
    padding: 0.5em;

    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    font-size: 1.2em;

    text-align: center;
    border: 1px solid #ccc;
    border-radius: 10px 10px 0px 0px;
}

#login form button{
    padding: 0.5em;
    cursor: pointer;
    border: none;
    background-color: #6354af;
    color: white;
    
    font-family: 'Rubik', sans-serif;
    font-weight: 500;
    font-size: 1.2em;
    border-radius: 0px 0px 10px 10px;
}
